@import url('https://fonts.googleapis.com/css2?family=Moon+Dance&display=swap');
.nav{
    position: absolute;
    top: 0;
    background: #fff;
    /* border: 3px solid red; */
    box-shadow: 2px 2px 4px rgba(0,0,0,0.3);
    transition: 0.2s;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items:center;
    padding: 0px 20px;
    /* font-family:Arial */
  
}
.nav .cont{
    height: 40px;
    display: flex;
    /* border: 1px solid red; */
    align-items:center;
    font-family: 'East Sea Dokdo', cursive;
    font-size: 1.7em;
    letter-spacing: 1px;
}
.nav .cont span{
    display: inline-block;
    padding: 2px;
    /* height: 40px; */
    margin-right: 10px;
    cursor: pointer;
}
.nav .cont span:nth-child(1){
    transform: scale(1.4);
    font-size: 2em;
}
.nav .cont span:nth-child(1):hover{
    color: rgb(13, 181, 248);
    transform: scale(1.5);
}
.nav .cont span:nth-child(2){
    font-weight: 500;
    font-size: 1.2em;
    /* line-height: 20px; */
}
.nav .info{
    /* border: 1px solid red; */
    width: 160px;
    display: flex;
    justify-content: space-between;
    height: 60px;
}
.nav .info .photo{
    border: 1px solid grey;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    overflow: hidden;
}
.nav .info .photo img{
    width: 100%;
   object-fit: fill;
    height: 100%;
}
.nav .info .userinfo{
    /* border: 1px solid red; */
    width: 90px;
    height: 100%;
}
.nav .info .userinfo span{
    display: inline-block;
    width: 100%;
    text-transform: capitalize;
    font-weight: 500;
    color: rgb(53, 50, 50);
    padding-left: 5px;
    height: 50%;
}